/* helpers */
/*input+label
    padding 0 5px
input[type=range]
    -webkit-appearance none
    height 6px
    outline none
    background-color $background

input[type="range"]::-webkit-slider-thumb 
    -webkit-appearance none
    position relative
    top 0px
    z-index 1
    width 16px
    height 16px
    cursor pointer
    border-radius 40px
    background limegreen*/
ul,
li {
  padding: 0;
  list-style-type: none;
}
a {
  text-decoration: none !important;
}
label {
  cursor: pointer;
}
.float-left {
  float: left;
}
.float-right {
  float: right;
}
.hidden {
  display: none;
}
.disabled {
  opacity: 0.7;
  pointer-events: none;
}
.row {
  margin: 0 !important;
}
.modal-open .modal {
  overflow: hidden;
  background: none;
}
.modal {
  position: relative;
}
.modal .close {
  font-family: 'wib-icomoon';
}
.modal-body {
  -webkit-box-shadow: 0px 10px 30px 0px rgba(0,0,0,0.2);
  -moz-box-shadow: 0px 10px 30px 0px rgba(0,0,0,0.2);
  box-shadow: 0px 10px 30px 0px rgba(0,0,0,0.2);
}
.modal-backdrop {
  opacity: 0 !important;
  background: #fff;
}
.nopadding {
  padding: 0;
}
/* rules */
html,
body {
  height: 100%;
  overflow: auto;
}
body {
  font-size: 16px;
  font-family: 'OpenSans', Sans-Serif;
  background-size: cover;
  background-color: #d7d8de;
  color: #6d747e;
  overflow: hidden;
}
body .wib-sim-app {
  max-width: 100%;
  margin: auto;
  clear: both;
  min-height: 100%;
  z-index: -10;
}
@media screen and (max-width: 1024px) {
  body {
    font-size: 14px;
  }
}
.fullscreen-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: 10;
}
.fullscreen-bg:after {
  content: ' ';
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 75;
  background: -moz-linear-gradient(top, rgba(255,255,255,0.95) 35%, rgba(255,255,255,0) 60%, rgba(255,255,255,0) 100%);
  background: -webkit-linear-gradient(top, rgba(255,255,255,0.95) 35%, rgba(255,255,255,0) 60%, rgba(255,255,255,0) 100%);
  background: linear-gradient(to bottom, rgba(255,255,255,0.95) 35%, rgba(255,255,255,0) 60%, rgba(255,255,255,0) 100%);
}
.fullscreen-bg video {
  object-fit: cover;
  width: 100vw;
}
.main {
  height: 100vh;
}
.main-title {
  font-size: 2.4rem;
  color: #6d747e;
  font-weight: 700;
  text-align: center;
  padding: 20px 0;
}
@media screen and (max-width: 1024px) {
  .main-title {
    padding: 10px 0;
  }
}
.content,
.tab-content {
  background-color: rgba(255,255,255,0.9);
  padding: 20px 0;
}
.content-el,
.tab-content-el {
  width: calc(100% - 40px);
  margin: 0 auto;
}
.content .welcome-content-el,
.tab-content .welcome-content-el,
.content .visionstrategygoals-content-el,
.tab-content .visionstrategygoals-content-el,
.content .kickingoffround-content-el,
.tab-content .kickingoffround-content-el {
  max-width: 750px;
  margin: 0 auto;
}
.content .welcome-content-el div,
.tab-content .welcome-content-el div,
.content .visionstrategygoals-content-el div,
.tab-content .visionstrategygoals-content-el div,
.content .kickingoffround-content-el div,
.tab-content .kickingoffround-content-el div {
  display: flex;
}
.content .welcome-content-el div .icon,
.tab-content .welcome-content-el div .icon,
.content .visionstrategygoals-content-el div .icon,
.tab-content .visionstrategygoals-content-el div .icon,
.content .kickingoffround-content-el div .icon,
.tab-content .kickingoffround-content-el div .icon {
  margin-right: 0.5em;
}
.pin-item-holder {
  display: flex;
  justify-content: center;
  width: 100%;
  overflow: hidden;
  flex-wrap: wrap;
  position: relative;
  padding-top: 20px;
}
@media screen and (max-width: 1024px) {
  .pin-item-holder {
    padding-top: 0;
  }
}
.pin-item-holder.unviewed.opened {
  height: 100%;
}
.pin-item-holder.viewed.opened {
  height: 100%;
}
.pin-item-holder .pin {
  position: relative;
  height: 100%;
}
.pin-item-holder .pin .wrapper {
  height: 100%;
  position: relative;
  display: flex;
  align-items: center;
  overflow: hidden;
}
.visionstrategygoals-content-review .content-el {
  max-width: 750px;
  margin: 0 auto;
}
.stakeholder-category-list .btn-group {
  margin-left: 12px;
}
.footer {
  background: #ffe600;
  color: #000;
  box-shadow: none;
  padding: 20px 0;
  text-align: center;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  cursor: pointer;
  text-transform: uppercase;
  border: none;
}
.footer:hover {
  background: #fff066;
}
.dashboard__main-metric {
  text-align: center;
}
.dashboard__main-metric .gauge-container {
  height: auto;
  width: 150px;
  margin: 0 auto;
}
@media only screen and (max-width: 1024px) {
  .dashboard__main-metric .gauge-container {
    width: inherit;
  }
}
.dashboard__main-metric .gauge-container .highcharts-container {
  width: 100% !important;
  text-align: center !important;
  overflow: visible !important;
}
.dashboard__main-metric .gauge-container .highcharts-container svg {
  overflow: visible;
}
.dashboard__main-metric .gauge-container .highcharts-container svg g.highcharts-axis > text {
  font-size: 1.3rem !important;
  text-transform: none !important;
  transform: translateY(-3.5rem);
  fill: #000 !important;
}
.highcharts-container {
  margin: 0 auto;
}
.gauge--solid {
  margin-right: 10px;
}
.solid-gauge-title {
  margin-right: 10px;
  line-height: 1.1em;
  position: relative;
  color: $lightblue;
  top: -20px;
  width: calc(100% - 20px);
  left: 10px;
}
.metric-holder .highcharts-container {
  height: 100px !important;
}
.metric-holder .highcharts-container >svg {
  height: 100px;
}
.metric__heatmap .highcharts-container {
  height: auto !important;
}
.metric__heatmap .highcharts-container >svg {
  height: auto;
}
.metric-balloon {
  color: #fff;
}
.end-of-round-feedback .highcharts-container {
  margin: auto;
}
.end-of-round-feedback .main-header {
  display: initial;
  text-align: center;
}
.end-of-round-feedback .modal-title {
  padding: 0px;
}
.end-of-round-feedback .eor-feedback-content .gauge-container .highcharts-container {
  margin: auto;
  width: 100% !important;
  text-align: center !important;
  overflow: visible !important;
}
.end-of-round-feedback .eor-feedback-content .gauge-container .highcharts-container svg {
  overflow: visible;
}
.end-of-round-feedback .eor-feedback-content .gauge-container .highcharts-container svg g.highcharts-axis > text {
  font-size: 1.3rem !important;
  text-transform: none !important;
  transform: translateY(-3.5rem);
  fill: #000 !important;
}
.end-of-round-feedback .metric__value {
  text-align: center;
  margin-bottom: 2rem;
  display: flex;
}
.end-of-round-feedback .metric__value data-metric {
  flex: 1;
  margin-right: 20px;
}
.end-of-round-feedback .metric__value data-metric:last-child {
  margin-right: 0;
}
.end-of-round-feedback .metric__value .metric-cont {
  width: 80px;
  margin: auto;
  font-weight: bold;
}
.end-of-round-feedback .metric__value .metric-cont .metric-values {
  float: none;
}
.end-of-round-feedback .client .client-heading {
  position: relative !important;
  background: none !important;
  padding: 1rem !important;
  color: $blue4 !important;
  font-size: 2rem !important;
}
.end-of-round-feedback .client .metric__values {
  width: 100%;
}
.end-of-round-feedback .client .metric__values .metric__value--label {
  float: left;
}
.end-of-round-feedback .client .metric__values .metric__value--data {
  float: right;
}
.end-of-round-feedback .client .metric__gauge {
  margin: auto;
  float: none;
  width: 100%;
}
.carousel.slide {
  height: 100%;
}
.carousel-inner {
  height: 100%;
}
.carousel-container {
  height: calc(100% - 60px);
}
.carousel-container .item.active {
  display: flex;
  flex-direction: column;
  height: 100%;
  background: linear-gradient(to bottom, rgba(67,73,81,0.95) 0%, rgba(67,73,81,0.65) 50%);
}
.carousel-container .item.active .chart-holder {
  padding-top: 20px;
}
.carousel-indicators {
  display: none;
}
.carousel-indicators li {
  border: 1px solid #ccc;
}
.carousel-indicators li.active {
  background-color: #ccc;
}
.carousel-text {
  max-width: 700px;
  margin: 0 auto;
  color: #fff;
  overflow-y: auto;
  overflow-x: hidden;
  margin-top: 30px;
}
.carousel-text h3 {
  text-align: center;
  text-transform: uppercase;
}
.carousel-control {
  position: absolute;
  background-repeat: no-repeat;
  background-size: 40px;
  height: 40px;
  top: 50%;
  opacity: 1;
  width: 40px;
  cursor: pointer;
}
.carousel-control.left {
  background-image: none;
  background: #ffe600;
  border-radius: 50%;
}
.carousel-control.left::after {
  content: "";
  font-family: 'wib-icomoon';
  font-size: 21px;
  color: #333;
  position: absolute;
  right: 11px;
  line-height: 40px;
}
.carousel-control.left.disabled {
  background-color: #ae9e0f;
  pointer-events: none;
}
.carousel-control.right {
  background-image: none;
  background: #ffe600;
  border-radius: 50%;
