.toggle-slider-control {
  margin-top: 40px;
  position: relative;
  display: block;
}
.toggle-slider-control .no {
  float: left;
  width: 40px;
  line-height: 40px;
  text-align: left;
}
.toggle-slider-control .yes {
  float: right;
  width: 40px;
  line-height: 40px;
  text-align: right;
}
.toggle-slider-control .toggle-slider .noUi-base {
  position: relative;
  height: 100%;
}
.toggle-slider-control .noUi-target {
  height: 40px;
  background: #f2f2f2;
  border-radius: 20px;
  box-shadow: 0px 7px 12px 2px rgba(0,0,0,0.2);
  width: calc(100% - 80px);
  float: left;
  position: relative;
}
.toggle-slider-control .noUi-horizontal .noUi-handle {
  width: 38px;
  height: 38px;
  top: 0;
  border-radius: 50%;
  background: #6d747e;
  color: #fff;
  cursor: pointer;
  box-shadow: none;
  text-transform: translate(-50%, 0);
  transform: rotateY(0deg);
  transition: background 0.3s ease-out, border 0.3s ease-out, transform 0.3s ease-out, color 0.3s ease-out;
}
.toggle-slider-control .noUi-handle:before {
  content: '';
  display: none;
}
.toggle-slider-control .noUi-handle:after {
  content: 'or';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  background: none;
  text-align: center;
  width: 38px;
  height: 38px;
  line-height: 38px;
}
.toggle-slider-control.toggle-yes .noUi-target {
  background: #00b6dd;
}
.toggle-slider-control.toggle-yes .noUi-handle {
  background: #fff;
  border: 3px solid #00b6dd;
  color: #00b6dd;
  transform: rotateY(180deg);
  font-family: 'wib-icomoon';
}
.toggle-slider-control.toggle-yes .noUi-origin {
