}
.toggle-slider-control.toggle-yes .noUi-handle:after {
  content: '';
}
.toggle-slider-control.toggle-no .noUi-target {
  background: #23569d;
}
.toggle-slider-control.toggle-no .noUi-handle {
  background: #fff;
  border: 3px solid #23569d;
